<template>
    <div class="content"><router-view></router-view>
        <mt-tabbar v-model="selected" class="tabBottom">
            <mt-tab-item :id="item.id" v-for="(item,key) in tabJson" @click.native="swapIndex(item.id)">
                <img slot="icon" :src="selected==item.id?item.selIcon:item.icon">{{item.label}}
            </mt-tab-item>
        </mt-tabbar>
    </div>
</template>

<script>
export default {
  data () {
    return {
      selected: 'home',
      tabJson: [
      {'id': 'home', 'label': '首页', 'icon': '/static/image/tabBottom/tab_home@3x.png', 'selIcon': '/static/image/tabBottom/tab_home_sel@3x.png'},
      {'id': 'shopCart', 'label': '购物车', 'icon': '/static/image/tabBottom/tab_shopping_cart@3x.png', 'selIcon': '/static/image/tabBottom/tab_shopping_cart_sel@3x.png'},
      {'id': 'custom', 'label': '我的', 'icon': '/static/image/tabBottom/tab_mine@3x.png', 'selIcon': '/static/image/tabBottom/tab_mine_sel@3x.png'}
      ]
    }
  },
  created: function () {
  },
  mounted: function () {
    this.selected = this.$route.name
  },
  methods: {
    swapIndex: function (val) {
      if(localStorage.userMobile == '' || localStorage.userMobile == undefined){
        if(val != 'home'){ //访问首页不需要登录
          this.$router.push({name: 'login'})
        }
      }else{
        this.$router.push({name: val})
      }
    }
  },
  watch:{
    $route: function (to, from) {
      this.selected = to.name 
    }
  }
}
</script>

<style scoped>
  .content {
  height: 100%;
  width: 100%;
}
.content {
  padding-bottom: .55rem;
}
</style>


